<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }

</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" v-model="form" label-width="120px">
            <el-form-item label="角色名称" prop="name">
                <el-input style="width: 500px" v-model="form.name" placeholder="请输入"></el-input>

            </el-form-item>

            <el-form-item label="角色描述" prop="description">
                <el-input style="width: 500px" v-model="form.description"
                          placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="排序">
                <el-input style="width: 200px" v-model="form.sort_num"
                          placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="状态">
                <el-select v-model="form.status" placeholder="请选择">
                    <el-option
                            label="启用"
                            :value="1">
                    </el-option>
                    <el-option
                            label="不启用"
                            :value="0">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item  style="margin-top: 50px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmitInfo">提交
                </el-button>
                <el-button plain style="margin-left: 50px"
                           @click="$router.back()">返回
                </el-button>
            </el-form-item>

        </el-form>
    </div>
</template>

<script>
    import api from '../../js/api/role'

    export default {
        data() {
            return {
                form: {},
                dataInfo: {},
                isNoEdit: true,
                isLoading: false,
            }
        },
        mounted() {
            if (this.$route.query.id !== undefined) {
                this.isNoEdit = true;
                this.loadData();
            } else {
                this.isNoEdit = false;
            }
        },
        methods: {
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                    this.dataInfo =api.deepCopy(this.form);
                })
            },


            edit() {
                this.isNoEdit = false;
            },
            onSubmitInfo() {
                this.isLoading = true;
                api.add(this.form, () => {
                    this.isLoading = false;
                    this.$message.success('保存成功');
                    this.$router.push({name: 'role'});
                    this.$route.meta.keepAlive = false;
                },()=>{
                    this.isLoading = false;
                })
            },
            cancelInfo() {
                this.isNoEdit = true;
                this.form = this.dataInfo;

            },
            back() {
            }
        }
    }
</script>
